import css from './css/style.scss'
import * as PIXI from 'pixi.js'
import {app} from './js/canvas'
import {load, loaded} from './js/loader'
import {loop, set_current_loop} from './js/loop'
import {main_menu} from './js/main_menu'


const whenLoadSuccess = () => {

  app.ticker.add((delta) => loop(delta))
  set_current_loop(main_menu)
  
  testtest()

}
window.addEventListener('loadSuccess', whenLoadSuccess, false)
load(new Event('loadSuccess'))











const testtest = () => {
  console.log('load ss load', loaded.images.pan)
  app.stage.addChild(loaded.images.pan)
  let pan = loaded.images.pan
  pan.width = pan.height = 80
  let rectangle = new PIXI.Graphics()
  rectangle.beginFill(0x660000)
  for (let i = 0; i < 100; i++) {
    for (let j = 0; j < 100; j++) {
      rectangle.drawRect(10 * i, 10 * j, 5, 5)
    }
  }
  rectangle.endFill()
  app.stage.addChild(rectangle)
}



